header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: rgb(99 99 99 / 10%) 0px 2px 8px 0px;
    z-index: 1000;
    
}

.navbar {
    position: relative;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (min-width: 992px) {
        justify-content: flex-start;
    }
}

.navbar-brand {
    padding-top: 0.3125rem;
    padding-bottom: 0.3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    text-decoration: none;
    white-space: nowrap;
    color: #111;

    @media (min-width: 992px) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    & span {
        text-transform: uppercase !important;
        font-size: 2.4rem;
        font-weight: 500;
        letter-spacing: 4px;
        padding-inline: 10px;
    }
}

.navbar-toggle {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 6px 14px;
    font-size: 18px;
    cursor: pointer;
    &:hover {
        background-color: #f8f8f8;
    }
    @media (min-width: 992px) {
        display: none;
    }
}

.navbar-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    @media (max-width: 991.9999px) {
        display: none;
    }

    &.active {
        display: flex !important;
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        width: 320px;
        border-right: 1px solid #ddd;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background: #fff;
        z-index: 1001;
        justify-content: flex-start;
        flex-direction: column;
        align-items: flex-start;

        & ul {
            flex-direction: column;
            width: 100%;

            & li {
                display: block;
                border-bottom: 1px solid #eee;
                padding: 12px 20px;
            }
        }
    }

    & ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
    }

    & a, & .link {
        color: #333;
        padding-inline: 8px;
        font-size: 1.8rem;
        display: block;

        &.active {
            color: #ff8700;
        }

        &:not(.active):hover {
            color: #000;
        }
    }
}