footer {
    background-color: #000;
    color: #999;
    padding-top: 2.5rem;
    padding-bottom: 4.5rem;

    & ul {
        list-style: none;
    }

    & h6 {
        margin-top: 2rem;
        color: #fff;
        line-height: 2.4rem;
    }
}

.footer-link {
    color: #999;
    line-height: 3rem;

    &:hover {
        color: #fff;
    }
}