.toggle-chatbox {
    position: fixed;
    bottom: 30px;
    right: 30px;
    background: #ffffff;
    color: #323133;
    width: 60px;
    height: 60px;
    font-size: 36px;
    line-height: 54px;
    border-radius: 50%;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    z-index: 1301;
    cursor: pointer;

    &:hover {
        color: #2196F3;
    }
}

.chatbox {
    position: fixed;
    bottom: 80px;
    right: 30px;
    width: 380px;

    
}


.chatbox-container {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 1px 2px 0 rgba(60, 64, 67, .3), 0 1px 3px 1px rgba(60, 64, 67, .15);
    margin: 0 auto 20px;
    overflow: hidden;
    margin-bottom: 40px;
}

.chatbox-header {
    padding: 15px 25px;
    border-bottom: 1px solid #eee;
    
    & button {
        font-size: 12px;
    }
}

.chatbox-content {
    max-height: 340px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 15px 25px;
    position: relative;
    min-height: 350px;
}

.chatbox-footer {
    border-top: 1px solid #eee;
    padding: 10px 20px;

    & input {
        border: 0;
        font-size: 14px;
    }

    & button {
        border: 1px solid #eee;
        padding: 2px 15px 2px 14px;
        &:hover {
            background-color: #fff;
            color: #2196F3;
        }
    }
}

.message {
    position: relative;
    display: inline-block;
    margin-bottom: 5px;
    color: #F9FBFF;
    font-size: 0.8em;
    padding: 10px 10px 10px 12px;
    border-radius: 20px;

    &.message--in {
        background: #b2b2b2;
    }

    &.message--out {
        background: #2196F3;
        float: right;
    }
}